.tabs-container {
  display: flex;
  justify-content: flex-start;
  gap: 8px;
  margin-bottom: 24px;
}

.tab-button {
  color: #333333;
}

.tab-button:hover {
  background-color: #F5F5F5;
}

.tab-button-active {
  background-color: #000000;
  color: #FFFFFF;
  border-color: transparent;
}

.tab-button-active:hover {
  background-color: #333333;
}
