.amount-section {
  margin-bottom: 24px;
}

.amount-section-title {
  margin-bottom: 16px;
  font-size: 0.875rem;
  color: #999999;
  font-weight: 500;
}

.amount-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  margin-bottom: 16px;
}

.amount-label {
  font-size: 1.8rem;
  color: #999999;
  margin-bottom: 8px;
}

.amount-input-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4px;
  border: 3px solid transparent;
  padding: 18px 12px 6px;
  background-color: #FFFFFF;
}

.amount-input-box.active {
  border-color: #0D99FF;
}

.amount-icon-button {
  padding: 4px;
}

.amount-icon {
  font-size: 1rem;
}

.amount-icon-dark {
  color: #333333;
}

.amount-icon-muted {
  color: #999999;
}
